import * as React from 'react';
import { css } from 'office-ui-fabric-react';

import styles from './MobilePreviewDimensionInput.module.scss';
import { IDevice } from '../mobilePreview/MobilePreview';
import strings from './MobilePreviewDimensionInput.resx';

export interface IMobilePreviewDimensionInputProps {
  currentDevice: IDevice;
  /* tslint:disable:no-any */
  onChangedX: (event: any) => void;
  onChangedY: (event: any) => void;
  /* tslint:enable:no-any */
}

export default class MobilePreviewDimensionInput extends React.Component<IMobilePreviewDimensionInputProps, void> {
  constructor(props: IMobilePreviewDimensionInputProps) {
    super(props);
  }

  public render(): React.ReactElement<{}> {
    const xContainerClassName: string = css(styles.mobilePreviewTextfieldXY, styles.xField);
    const yContainerClassName: string = css(styles.mobilePreviewTextfieldXY, styles.yField);
    const textfieldsClass: string = css(styles.xyTextfields, 'ms-borderColor-neutralTertiaryAlt',
      'ms-fontColor-neutralSecondary');
    const labelsClass: string = css(styles.xyLabels, 'ms-fontColor-neutralTertiaryAlt');

    return(
      <div>
        <div className={ xContainerClassName }>
          <label className={ labelsClass }>{ strings.XDimension }</label>
          <input className={ textfieldsClass } onChange={ this.props.onChangedX }
              value={ this.props.currentDevice.width }></input>
        </div>
        <div className={ yContainerClassName }>
          <label className={ labelsClass }>{ strings.YDimension }</label>
          <input className={ textfieldsClass } onChange={ this.props.onChangedY }
              value={ this.props.currentDevice.height }></input>
        </div>
      </div>
    );
  }
}